.newletter {
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: darkblue;
  padding: 60px 0;
  color: #fff;
}
.newletter .right {
  border-left-width: 95%;
}
a{
  text-decoration: none;
  color: black;
}

.newletter h1 {
  color: #fff;
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 15px;
}
.newletter .right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.newletter input {
  width: 100%;
  padding: 20px;
  border: none;
  outline: none;
  height: 30px;
  margin-left: 5%;
  margin-right: 5%;
}
.newletter i {
  padding: 12px;
  color: darkblue;
  background-color: #fff;
  margin-left: 2%;
  margin-right: 5%;
}
footer {
  background-color: #eeeeee;
}
footer .container {
  display: flex;
  justify-content: space-between;
  max-width: 800px;
  margin: 0 auto;
  background: white;
  padding: 10px 0;
}
@media screen and (max-width: 7000px) {
  footer .container {
    grid-template-columns: repeat(2, 1fr);
  }
}
footer .logo span {
  color: darkblue;
  font-size: 14px;
}
footer .logo p {
  color: grey;
  margin: 30px 0 15px 0;
}
footer .logo .icon {
  background-color: darkblue;
  color: white;
  margin-right: 10px;
}
footer h3 {
  margin-bottom: 40px;
  font-weight: 500;
}
footer ul li {
  margin-bottom: 20px;

}
footer .box-link {
  list-style: none;
}
footer .items {
  margin-bottom: 3px;
}
footer .items img {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  margin-right: 10px;
  object-fit: cover;
}
footer .items span,
footer .items i {
  list-style: none;
  font-size: 12px;
  color: #1eb2a6;
  margin-right: 5px;
  text-transform: capitalize;
}
footer h4 {
  list-style: none;
  font-weight: 400;
  margin-top: 5px;
}
footer .last ul li {
  display: flex;
  margin-bottom: 50px;
}
footer .last i {
  width: 60px;
  padding-top: 10px;
  font-size: 40px;
  color:black;
  list-style-type: none;
}
.legal {
  text-align: center;
  padding: 25px 0;
  color: grey;
  background-color: #e9e9e9;
}
.legal p {
  font-size: 15px;
}
.legal i {
  color: #1eb2a6;
}

